@import url('https://fonts.cdnfonts.com/css/digital-numbers');

* {
  margin: 0;
  padding: 0;
  font-family: 'Helvetica Neue';
}

body {
  /* min-height: 100vh; */
  height: calc(var(--vh, 1vh) * 100);
  background-repeat: no-repeat;
  background-image: radial-gradient(
      80% 80% at 30% 10%,
      rgba(252, 217, 0, 0.6) 0%,
      rgba(252, 217, 0, 0) 100%
    ),
    radial-gradient(
      55% 55% at 20% 40%,
      rgba(232, 99, 10, 0.6) 0%,
      rgba(252, 217, 0, 0) 100%
    ),
    radial-gradient(
      55% 55% at 78% 15%,
      rgba(232, 99, 10, 0.4) 0%,
      rgba(252, 217, 0, 0) 100%
    ),
    radial-gradient(
      15% 15% at 85% 65%,
      rgba(3, 83, 151, 0.5) 0%,
      rgba(255, 255, 255, 0) 100%
    ),
    radial-gradient(
      80% 80% at 60% 87%,
      rgba(0, 30, 108, 0.7) 0%,
      rgba(0, 10, 255, 0) 100%
    );
}
